.navbar{width:100%;position:-webkit-sticky;position:sticky;top:0;z-index:10;background:#00000a}.navbar .links{display:flex;justify-content:center}.navbar .links a{transition:1s cubic-bezier(0.075, 0.82, 0.165, 1);font-size:32px;color:rgba(255,255,225,0.5);padding:10px 20px}@media all and (max-width: 480px){.navbar .links a{font-size:24px}}.navbar .links a:hover{color:#EAC440;cursor:pointer}

.projects{width:100%;background:#fff;padding:50px 0 0 0;position:relative}.projects .heading{display:flex;flex-direction:column;align-items:center;margin-bottom:50px}.projects .heading h1{text-align:center;margin-bottom:10px;font-size:64px;color:#00000a}.projects .project-cards-container .project-card{width:1000px;margin:auto;display:flex;justify-content:space-evenly;align-items:center;flex-wrap:wrap;margin-bottom:50px;padding:50px;background:#fff;box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12)}.projects .project-cards-container .project-card .image-two{order:1}.projects .project-cards-container .project-card .image{width:400px;height:225px;border:2px solid #00000a;object-fit:cover}@media all and (max-width: 480px){.projects .project-cards-container .project-card .image{margin:0px}}.projects .project-cards-container .project-card .details{width:400px}.projects .project-cards-container .project-card .details h1{font-size:28px;color:#00000a;margin-bottom:25px}.projects .project-cards-container .project-card .details .icon{font-size:24px}.projects .project-cards-container .project-card .details .stack{color:#554409}.projects .project-cards-container .project-card .details p{margin-bottom:25px;text-align:justify}.projects .project-cards-container .project-card .details button{margin-right:15px;background:#EAC440;padding:5px;border:none;color:#00000a;font-size:18px}.projects .project-cards-container .project-card .details button:hover{background:#00000a;color:#EAC440}@media all and (max-width: 1000px){.projects .project-cards-container .project-card{width:600px}}@media all and (max-width: 768px){.projects .project-cards-container .project-card{flex-direction:column;width:auto;padding:20px 10px}.projects .project-cards-container .project-card .image{margin-bottom:20px}.projects .project-cards-container .project-card .image-two{order:0}}.projects .project-cards-container .line{width:100%;height:2px;background:#EAC440}

.about{width:100%;background:#EAC440;padding:50px 0 50px 0;box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12)}.about .heading{display:flex;flex-direction:column;align-items:center}.about .heading h1{text-align:center;margin-bottom:10px;font-size:64px;color:#00000a}.about .heading .line{width:100px;height:5px;text-align:center;background:#00000a}.about .description{max-width:720px;margin:auto;margin-top:50px;text-align:center}@media all and (max-width: 768px){.about .description{max-width:400px}}.about .description p{color:#00000a;font-size:24px}.about .description a{padding:10px;font-size:24px;background:#00000a;color:#EAC440;transition:1s cubic-bezier(0.075, 0.75, 0.15, 1)}.about .description a:hover{border:2px solid #00000a;background:#EAC440;color:#00000a}.about .tech-stack{max-width:480px;margin:auto;margin-top:50px;padding:15px;border-radius:25px;background:#000024}.about .tech-stack .heading{margin-bottom:25px}.about .tech-stack .heading h3{padding-left:5px;text-align:center;font-size:32px;color:#fff;border-left:3px solid #fff}.about .tech-stack .items{text-align:center}.about .tech-stack .items .item{font-size:48px;color:#00000a;margin:0 5px;transition:1s cubic-bezier(0.075, 0.75, 0.15, 1)}

.contact{width:100%;background:#fff;padding:50px 0 100px 0}.contact .heading{display:flex;flex-direction:column;align-items:center;margin-bottom:50px}.contact .heading h1{text-align:center;margin-bottom:10px;font-size:64px;color:#00000a}.contact .heading .line{width:100px;height:5px;text-align:center;background:#00000a}.contact .links{display:flex;justify-content:center;flex-wrap:wrap}.contact .links .item{margin:0 25px}.contact .links .item:hover svg{color:#EAC440;background:#00000a}.contact .links .item svg{width:150px;height:150px;padding:40px;text-align:center;line-height:75px;background:#EAC440;border-radius:100%;transition:1s cubic-bezier(0.075, 0.82, 0.165, 1)}.contact .links .item p{display:block;text-align:center}

.footer{width:100%;background:#EAC440;padding:50px 0 100px 0}.footer .text{text-align:center;font-size:32px}

