    /* background by SVGBackgrounds.com */
    @media all and (max-width: $mobile) {
      padding-top: 100px;
    }
    .header {
      margin-bottom: 50px;
      
      h1 {
        font-size: 64px;
        color: $color-accent-primary;
        @media all and (max-width: $mobile) {
          font-size: 32px;
        }
      }
      p {
        font-size: 32px;
        color: $color-light;
        @media all and (max-width: $mobile) {
          font-size: 24px;
        }
      }
    }
    .links {
      flex: 1 0 100%;
      display: flex; justify-content: center;
      @media all and (max-width: $mobile) {
        flex-direction: column; justify-content: flex-start;
      }
      a {
        height: 60px;
        padding: 10px 30px;
        font-size: 24px;
        color: $color-light;
        transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
        &:hover {
          color: $color-dark;
          background: $color-accent-primary;
          cursor: pointer;
        }
      }
      :nth-child(1) {
        border-left: 2px solid $color-accent-primary;
      }
      :nth-child(2) {
        border: 2px solid $color-accent-primary;
      }
      :nth-child(3) {
        border-right: 2px solid $color-accent-primary;
      }
    }
  }